<template>
    <div>
        <el-card>
            <el-form :model="store" class="demo-form-inline" v-if="!isput">
                <el-form-item label="头像">
                    <el-avatar :size="70" :src="user.image_url" />
                </el-form-item>
                <el-form-item label="姓名">
                    {{ user.name }}
                </el-form-item>
                <el-form-item label="邮箱">
                    {{ user.email }}
                </el-form-item>
                <el-form-item label="电话">
                    {{ user.phone }}
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">修改个人信息</el-button>
                </el-form-item>
            </el-form>
            <el-form :model="store" class="demo-form-inline" v-if="isput">
                <el-form-item label="头像">
                    <el-upload v-model:file-list="PicList" name="work_file" action="http://127.0.0.1:11002/uploadImage"
                        :show-file-list="false" :on-success="uploadSuccess" :on-remove="handleRemove">
                        <el-avatar :size="70" :src="operaForm.image_url" />
                    </el-upload>
                    <el-dialog v-model="dialogVisible">
                        <img w-full :src="dialogImageUrl" alt="Preview Image" />
                    </el-dialog>
                </el-form-item>
                <el-form-item label="姓名">
                    <el-input v-model="operaForm.name" />
                </el-form-item>
                <el-form-item label="邮箱">
                    <el-input v-model="operaForm.email" placeholder="Approved by" />
                </el-form-item>
                <el-form-item label="电话">
                    <el-input v-model="operaForm.phone" placeholder="Approved by" />
                </el-form-item>
                <el-form-item label="密码">
                    <el-button type="primary" @click="updatePassword">修改密码</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="putUpdate">修改</el-button>
                    <el-button type="primary" @click="onSubmit">取消</el-button>
                </el-form-item>
            </el-form>

        </el-card>
        <el-dialog :modal-append-to-body="false" v-model="operaDialog" :title="修改密码" @close="resetForm">
            <el-form status-icon :model="operaFormPassword" ref="operaFormPassword" class="operaDialog" label-width="100px"
                :rules="rules">
                <el-form-item label="旧密码" prop="name">
                    <el-input placeholder="请输入旧密码" v-model="operaFormPassword.password" show-password></el-input>
                </el-form-item>
                <el-form-item label="新密码" prop="email">
                    <el-input placeholder="请输入新密码" v-model="operaFormPassword.new_password" show-password></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="updatePasswordSubmit()">提交</el-button>
                    <el-button @click="resetForm">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>
<script>
import {
    getByToken,
    updateManager,
    updateManagerPassword,
} from '@/api/manager/api.js'
import { store } from "@/utils/global.js";
// eslint-disable-next-line no-unused-vars
export default {
    data() {
        return {
            isput: false,
            store,
            Piclist: [],
            user: {
                id: "",
                name: "",
                image_url: "",
                email: "",
                phone: "",
            },
            operaForm: {
                id: "",
                name: "",
                image_url: "",
                email: "",
                phone: "",
                password: "",
            },
            operaDialog: false,
            operaFormPassword: {
                password: "",
                new_password: "",
            }

        }
    },
    created() {
        this.getUserMessage()
    },
    methods: {
        async getUserMessage() {
            getByToken().then(res => {
                if (res.data.code != 200) {
                    this.$message.error(res.data.msg);
                } else {
                    this.user = res.data.data
                    this.operaForm = { ...res.data.data }

                    // this.$router.push('/manager').catch(() => { })
                }
            })
        },
        addData() {
            createApi(this.operaForm).then((res) => {
                if (res.data.code === 200) {
                    this.$message({
                        showClose: true,
                        message: res.data.msg,
                        type: "success",
                    })
                    this.operaDialog = false
                    this.getDatas()
                }
            })
        },
        uploadSuccess(file, fileList) {
            this.operaForm.image_url = file.data
            this.$message({
                showClose: true,
                message: "上传成功",
                type: "success",
            })
        },
        onSubmit() {
            this.isput = !this.isput
        },
        putUpdate() {
            updateManager(this.operaForm).then(res => {
                if (res.data.code != 200) {
                    this.$message.error(res.data.msg);
                } else {
                    this.$message({
                        showClose: true,
                        message: "修改成功",
                        type: "success",
                    })
                    this.getUserMessage()

                    sessionStorage.setItem("name", this.operaForm.name);
                    sessionStorage.setItem("image_url", this.operaForm.image_url);
                    this.isput = !this.isput
                    // 组件没有重新加载 可能要优化
                    this.$router.push('/manager').catch(() => { })
                }
            })
        },
        updatePassword() {
            this.operaDialog = true
        },
        resetForm() {
            this.$refs.operaFormPassword.clearValidate()
            this.operaFormPassword = {};
            this.operaDialog = false
        },
        updatePasswordSubmit() {
            updateManagerPassword(this.operaFormPassword).then(res => {
                if (res.data.code != 200) {
                    this.$message.error(res.data.msg);
                } else {
                    this.$message({
                        showClose: true,
                        message: "修改成功",
                        type: "success",
                    })
                }
                this.operaDialog = false
            })
        },
    },
};
</script>
  
<style></style>
  